::-webkit-scrollbar {
  height: 4px;
  width: 0px;
}
.terminal-container {
  width: 100%;
  display: flex;
  overflow: auto;
  min-height: 100vh;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.terminal-text {
  color: rgb(255, 255, 255);
  width: 1920px;
  height: 136px;
  text-align: center;
  line-height: 4;
  background-image: linear-gradient(90deg, rgb(1, 43, 70) 2%, rgb(0, 0, 0) 100%);
}
.terminal-container1 {
  width: 100%;
  height: 1006px;
  display: flex;
  position: relative;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-image: linear-gradient(45deg, rgb(0, 69, 113) 1.00%,rgb(0, 0, 0) 96.00%);
}
.terminal-container2 {
  flex: 0 0 auto;
  width: 939px;
  height: 50%;
  display: flex;
  align-items: flex-start;
}
.terminal-server-list {
  width: 162px;
  height: 451px;
  align-self: flex-end;
  margin-top: 0px;
  border-color: #ffffff;
  border-width: 1px;
  padding-left: 0px;
  margin-bottom: 0px;
  color: white;
  background-color: #000000;
  list-style: none;
}
.terminal-server-list .e-list-item{
 
  color: white;
  background-color: #000000;
  list-style: none;
}

.terminal-server-list .e-list-item.e-hover {
  color: #077700;
  box-shadow: 0px 0px 50px 0px #23ff00;
  background-color: #00b53b;
  cursor: pointer;
}


.terminal-container3 {
  flex: 0 0 auto;
  width: 581px;
  height: auto;
  display: flex;
  flex-direction: column;
}
.terminal-container4 {
  flex: 0 0 auto;
  width: 581px;
  height: auto;
  display: flex;
  flex-direction: column;
}
.terminal-container5 {
  flex: 0 0 auto;
  width: 581px;
  height: auto;
  display: flex;
  margin-top: 52px;
  flex-direction: column;
}
.terminal-terminal-list {
  width: 100%;
  height: 417px;
  margin-top: 0px;
  border-color: #ffffff;
  border-width: 1px;
  color: white;
  padding-left: 0px;
  margin-bottom: 0px;
  background-color: #000000;
  font-size: 12px;
  overflow:hidden; overflow-y:scroll; overflow-x: scroll;

}
.terminal-li1 {
  color: #ffffff;
  font-size: 6px;
}
.terminal-container6 {
  flex: 0 0 auto;
  width: 100%;
  height: auto;
  display: flex;
  position: relative;
  align-self: center;
  align-items: flex-start;
  flex-direction: row;
  justify-content: center;
}
.terminal-command-input {
  color: #ffffff;
  width: 466px;
  height: 34px;
  border-color: #ffffff;
  border-radius: 0px;
  background-color: #000000;
}
.terminal-execute {
  color: rgb(0, 0, 0);
  width: 117px;
  height: 34px;
  align-self: flex-end;
  box-shadow: 0px 0px;
  text-align: center;
  transition: 0.3s;
  line-height: 2;
  padding-top: 0px;
  border-color: #ffffff;
  padding-left: 0px;
  border-radius: 0px;
  padding-right: 0px;
  padding-bottom: 0px;
  background-color: rgb(0, 156, 48);
}
.terminal-execute:hover {
  color: #077700;
  box-shadow: 0px 0px 50px 0px #23ff00;
  background-color: #00b53b;
}
.terminal-execute:active {
  color: #00ff33;
  box-shadow: 0px 0px 60px 8px #06ff00;
  background-color: #03de45;
}
.terminal-player-list {
  width: 191px;
  height: 451px;
  align-self: flex-end;
  margin-top: 0px;
  border-color: #ffffff;
  border-width: 1px;
  padding-left: 0px;
  margin-bottom: 0px;
  background-color: #000000;
  color: rgb(255, 255, 255);
  overflow:hidden; overflow-y:scroll; overflow-x: scroll;
}
.terminal-li2 {
  color: #ffffff;
  font-size: 12px;
}
