.home-container {
  width: 100%;
  display: flex;
  overflow: auto;
  min-height: 100vh;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  background-image: linear-gradient(0deg, rgb(12, 13, 13) 0.00%,rgb(32, 32, 32) 100.00%);
}
.home-header {
  display: flex;
  max-width: var(--dl-size-size-maxwidth);
  align-self: center;
  align-items: flex-end;
  padding-top: 125px;
  padding-right: var(--dl-space-space-halfunit);
  justify-content: center;
}
.home-image {
  left: -930px;
  width: 54px;
  bottom: -168px;
  height: 52px;
  position: absolute;
  border-radius: var(--dl-radius-radius-round);
  padding-right: 0px;
}
.home-text {
  color: #ffffff;
  width: auto;
  height: auto;
}
.home-image1 {
  width: 88px;
  height: 80px;
  margin-top: var(--dl-space-space-sixunits);
  object-fit: cover;
  border-radius: var(--dl-radius-radius-round);
}
.home-button {
  color: #ffffff;
  box-shadow: 0px 0px 40px 5px rgba(0, 31, 255, 0.41);
  margin-top: var(--dl-space-space-sixunits);
  transition: 0.3s;
  font-family: Inter;
  border-color: #15323f;
  border-style: solid;
  border-width: 2px;
  background-color: #000000;
}
.home-button:hover {
  box-shadow: 0px 0px 50px 8px rgba(0, 31, 255, 0.41);
}
.home-button:active {
  box-shadow: 0px 0px 60px 12px rgba(0, 31, 255, 0.41);
  background-color: #1f1ad2;
}
@media(max-width: 1600px) {
  .home-header {
    flex: 0;
    width: auto;
    position: relative;
    max-width: auto;
  }
  .home-text {
    color: rgb(255, 255, 255);
    border-color: rgba(0, 0, 0, 0);
    border-width: 1px;
  }
  .home-image1 {
    margin-top: var(--dl-space-space-sixunits);
  }
  .home-button {
    color: rgb(255, 255, 255);
    box-shadow: 0px 0px 10px 2px rgba(0, 31, 255, 0.41);
    font-style: normal;
    font-family: Inter;
    font-weight: 800;
    text-transform: capitalize;
    background-color: rgb(0, 0, 0);
  }
}
@media(max-width: 1200px) {
  .home-header {
    width: 100%;
    height: 267px;
    align-self: center;
    padding-top: 0px;
    padding-left: 0px;
    padding-bottom: 0px;
  }
  .home-text {
    align-self: center;
    margin-top: var(--dl-space-space-sixunits);
    margin-left: var(--dl-space-space-sixunits);
    margin-right: var(--dl-space-space-sixunits);
    margin-bottom: var(--dl-space-space-sixunits);
  }
}
@media(max-width: 991px) {
  .home-header {
    align-self: center;
  }
  .home-text {
    margin-top: 350px;
  }
}
@media(max-width: 767px) {
  .home-header {
    align-self: center;
    padding-left: var(--dl-space-space-twounits);
    padding-right: var(--dl-space-space-twounits);
  }
  .home-text {
    margin-top: 350px;
  }
}
@media(max-width: 479px) {
  .home-header {
    width: auto;
    height: auto;
    padding: var(--dl-space-space-unit);
    max-width: auto;
    align-self: center;
    justify-content: center;
  }
  .home-text {
    margin-top: 350px;
  }
}
