.login-container {
  width: 100%;
  display: flex;
  z-index: 99;
  overflow: auto;
  min-height: 100vh;
  align-items: center;
  border-color: var(--dl-color-gray-black);
  border-width: 0px;
  flex-direction: row;
  justify-content: flex-end;
}
.login-container1 {
  width: 100%;
  height: 969px;
  display: flex;
  z-index: 99;
  margin-top: 0px;
  align-items: flex-end;
  flex-direction: column;
  justify-content: flex-start;
  background-image: linear-gradient(90deg, rgb(34, 34, 34) 0.00%,rgb(44, 62, 80) 100.00%);
}
.login-image {
  left: -7px;
  width: 1920px;
  bottom: -24px;
  height: 1012px;
  opacity: 0.1;
  z-index: -1;
  position: absolute;
  object-fit: cover;
}
.login-input {
  box-shadow: 0px 0px 10px 0px #0e185f;
  margin-top: 450px;
  transition: 0.3s;
  margin-right: 175px;
  background-color: rgba(35, 39, 53, 0.68);
}
.login-input:focus {
  box-shadow: 0px 0px 50px 0px #19347e;
  border-color: #152a50;
  border-width: 1px;
}
.login-input:focus-within {
  border-color: #ff0000;
}
.login-button {
  width: var(--dl-size-size-large);
  height: 10%;
  box-shadow: 0px 0px 0px 0px #000000;
  margin-top: 30px;
  text-align: center;
  transition: 0.3s;
  border-color: #000000;
  border-width: 0px;
  margin-right: 225px;
  padding-right: 0.5rem;
  background-color: transparent;
}
.login-button:hover {
  color: #0a738e;
  box-shadow: 0px 0px 10px 0px #0a428e;
  border-color: #0a428e;
}
.login-button:active {
  color: #0033bd;
  box-shadow: 0px 0px 50px 7px #0a2e8e;
  border-color: #003681;
  background-color: #183da0;
}
.login-textinput {
  box-shadow: 0px 0px 10px 0px #0e185f;
  margin-top: 25px;
  transition: 0.3s;
  margin-right: 100px;
  margin-bottom: 395px;
  background-color: rgba(35, 39, 53, 0.68);
}
.login-textinput:focus {
  box-shadow: 0px 0px 50px 0px #0e185f;
}
.login-textinput:focus-within {
  border-color: #ff0000;
}
@media(max-width: 1600px) {
  .login-container1 {
    width: 100%;
    height: 1006px;
    background-image: linear-gradient(90deg, rgb(34, 34, 34) 0.00%,rgb(44, 62, 80) 100.00%);
  }
  .login-image {
    width: 1600px;
  }
  .login-input {
    margin-right: 100px;
  }
  .login-button {
    margin-right: 160px;
  }
  .login-textinput {
    margin-right: 100px;
  }
}
@media(max-width: 1200px) {
  .login-container1 {
    background-image: linear-gradient(90deg, rgb(34, 34, 34) 0.00%,rgb(44, 62, 80) 100.00%);
  }
  .login-image {
    width: 1200px;
  }
  .login-input {
    margin-right: 75px;
  }
  .login-button {
    margin-right: 130px;
  }
  .login-textinput {
    margin-right: 75px;
  }
}
@media(max-width: 991px) {
  .login-container1 {
    background-image: linear-gradient(90deg, rgb(34, 34, 34) 0.00%,rgb(44, 62, 80) 100.00%);
  }
  .login-image {
    width: 991px;
  }
  .login-input {
    margin-right: 25px;
  }
  .login-button {
    box-shadow: 0px 0px;
    margin-right: 90px;
    background-color: transparent;
  }
  .login-textinput {
    margin-right: 25px;
  }
}
@media(max-width: 767px) {
  .login-container1 {
    position: relative;
    background-image: linear-gradient(90deg, rgb(34, 34, 34) 0.00%,rgb(44, 62, 80) 51.00%);
  }
  .login-image {
    width: 767px;
    opacity: 0.02;
  }
  .login-input {
    margin-right: 25px;
  }
  .login-button {
    margin-right: 80px;
  }
  .login-textinput {
    margin-right: 25px;
  }
}
@media(max-width: 479px) {
  .login-container1 {
    width: 100%;
    height: 1035px;
    position: relative;
    padding-right: 0px;
    background-image: linear-gradient(90deg, rgb(34, 34, 34) 1.00%,rgb(44, 62, 80) 55.00%);
  }
  .login-image {
    width: 473px;
    height: 1063px;
    opacity: 0.02;
  }
  .login-input {
    margin-right: 25px;
  }
  .login-button {
    width: 162px;
    height: 31px;
    text-align: center;
    line-height: 1.65;
    padding-top: 0px;
    margin-right: 73px;
    padding-left: 0px;
    padding-right: 0px;
    padding-bottom: 0px;
  }
  .login-textinput {
    margin-right: 25px;
  }
}
